body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* custom template */
html, body {
  /* height: 100%; */
  font-family: Dotum, "\B9D1\C740   \ACE0\B515", "roboto", "Helvetica Neue", Helvetica, Arial, "\B9D1\C740   \ACE0\B515", malgun gothic, "\B3CB\C6C0", Dotum, sans-serif;
  color: #202B3D;
  background-color: #e9eaed;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.5;
  height: 100%;
}

.btn-primary, .label-primary, .list-group-item.active, .list-group-item.active:hover, .list-group-item.active:focus {
  background-color: #3B5999;
  color: #fffffe;
  border-color: unset;
  font-weight: 800;
}
.btn-primary:hover, .btn-primary:focus, .btn-primary:active, .btn-primary.active, .open .dropdown-toggle.btn-primary {
  color: #fff;
  background-color: #3276b1;
  /* border-color: #285e8e; */
}
.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active, .show>.btn-primary.dropdown-toggle {
  color: #fff;
  background-color: #3276b1;
  /* border-color: #285e8e; */
}

a {
  color: #222222;
}

a:hover {
  text-decoration: none;
}

hr {
  border-color: #dedede;
}

.post-modal .modal-header, .modal-footer {
  background-color: #f2f2f2;
  font-weight: 800;
  font-size: 12px;
}

.post-modal .modal-body textarea.form-control {
  resize: none;
  border: 0;
  box-shadow: 0 0 0;
}

.post-modal .modal-header, .modal-footer {
  background-color: #f2f2f2;
  font-weight: 800;
  font-size: 12px;
}

.form-group .form-control, .form-group input, .form-group textarea {
  padding-right: 64px;
}
.form-group textarea {
  resize: none;
}
.panel textarea, .well textarea, textarea.form-control {
  resize: none;
}

.bg-blue {
  border-width: 0;
  background-color: #3B5999;
  color: #ffffff;
  width: inherit;
}

.bg-blue li > a, .navbar-toggler .navbar-toggler-icon {
  color: #efefef;
  cursor: pointer;
}

.bg-blue .dropdown-menu li a {
  color: #2A4888;
}

.bg-blue .dropdown-menu li > a {
  padding-left: 32px;
}

.bg-blue li > a:hover, .bg-blue li > a:focus, .bg-blue .open, .bg-blue .open > a, .bg-blue .open > a:hover, .bg-blue .open > a:focus {
  background-color: #2A4888;
  color: #fff;
}

.navbar-brand img {
  float: left;
  width: 24px;
  height: auto;
}

.navbar-nav {
  margin-left: 20px;
}

.navbar {
  padding: 0 16px;
}

.navbar-dark .navbar-toggler {
  color: white;
  font-weight: 800;
  font-size: 13px;
}

.navbar-dark .navbar-nav .nav-link {
  color: white;
  font-weight: 800;
  font-size: 12px;
  line-height: 34px;
  padding: 8px 12px;
}
.ui-box {
  margin-top: 16px;
  background-color: #fff;
  border-radius: 5px;
  border: 1px solid #e6ecf5;
  margin-bottom: 16px;
  width: 100%;
}

.ui-box:first-child {
  margin-top: 32px;
}

.contents {
  position: relative;
  padding: 40px;
  border-bottom: 1px solid #e6ecf5;
}

.contents p {
  margin: 24px 0;
  font-size: 1.125em;
}

.more {
  position: relative;
  transition: all .3s ease;
  color: #888da8;
  fill: #c0c4d8;
}

.contents-accounts {
  margin-bottom: 20px;
}

.contents-accounts img {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  overflow: hidden;
}

.inline-items > * {
  display: inline-block;
  vertical-align: middle;
}

.contents-date {
  margin-left: 8px;
}

.contents .contents-date a, .contents .contents-accounts-name {
  font-weight: 700;
  font-size: 1.35em;
}

.contents-accounts .more {
  float: right;
  font-size: 16px;
}

.more i {
  width: 24px;
  height: 24px;
  color: #888da8;
}

.more i:hover {
  cursor: pointer;
  opacity: 0.4;
}

.featured-dropdown {
  display: block;
  position: absolute;
  top: 100%;
  right: 0;
  width: 180px;
  box-shadow: 0 0 34px 0 rgba(63,66,87,.1);
  background-color: #fff;
  border-radius: 4px;
  visibility: hidden;
  opacity: 1;
  transition: visibility 0s linear .3s,opacity .3s linear;
  text-align: left;
  padding: 16px 24px;
  z-index: 5;
}

.featured-dropdown a {
  padding: 8px 0;
  font-size: 12px;
  color: #515365;
  display: block;
  font-weight: 700;
}

.featured-dropdown ul {
  padding: 16px 24px;
  margin-bottom:0;
}
.featured-dropdown li {
  list-style: none;
}
.featured-dropdown li a>* {
  display:inline-block;
  vertical-align:middle;
}

.featured-dropdown a:hover {
  color:#2a6496;
}

.more:hover .featured-dropdown {
  visibility:visible;
  opacity:1;
  transition-delay:0s
}

.featured-dropdown .noti-list {
  padding: 0;
}

.noti-list .noti-text {
  padding-left: 16px;
  width: 76%;
  color: #202B3D;
}

.contents-info {
  padding: 24px 0 0;
  border-top: 1px solid #e6ecf5;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-size: 12px;
}

.like-and-reply {
  display: inline-block;
  vertical-align: middle;
}
.contents-info .contents-icon, .comments .contents-icon {
  display: inline-block;
  margin-right: 24px;
  vertical-align: middle;
  font-size: 12px;
}
.contents-info .reply, .comments .reply {
  margin-top: 2px;
  vertical-align: middle;
  font-size: 12px;
}

.contents-info .contents-icon svg, .comments .contents-icon svg, .contents-info .reply svg, .comments .reply svg {
  margin-right: 4px;
}
@media (min-width: 1024px) {
  .container {
      max-width: 900px;
  }
}

@media (min-width: 800px) {
  .container {
      max-width: 680px;
  }
}

@media (min-width: 540px) {
  .container {
      max-width: 600px;
  }
}

.dropdown-title:first-child {
    border-top: none;
}
.dropdown-title {
    padding: 16px 24px;
    line-height: 1;
    border-bottom: 1px solid #e6ecf5;
    border-top: 1px solid #e6ecf5;
    display: table;
    width: 100%;
    position: relative;
    border-radius: 5px 5px 0 0;
}

.dropdown-title > * {
    margin-bottom: 0;
    display: table-cell;
    vertical-align: middle;
    font-size: 12px;
}


.dropdown-title.dropdown-title-small .title {
    font-size: 12px;
    color: #202B3D;
}

.featured-dropdown ul, .nav-noti .dropdown-title {
    padding: 0;
    height: 300px;
    overflow: scroll;
}

.friend-requests {
    margin-bottom: 0;
    padding: 0;
}

.noti-list li {
    padding: 25px;
    border-bottom: 1px solid #e6ecf5;
    display: block;
    position: relative;
    transition: all .3s ease;
}

.noti-list li:hover {
    background-color: #fafbfd;
}

.user-thumb {
    display: inline-block;
    position: relative;
}

.user-thumb img {
    border-radius: 100%;
    overflow: hidden;
    max-width: unset;
    height: 40px;
    width: 40px;
}

.noti-list li > * {
    margin-bottom: 0;
    display: inline-block;
    vertical-align: middle;
}

.login-title, .signup-title {
    margin-top: 50px;
}

.form-signin, .form-signup {
    max-width: 320px;
    padding: 8px;
    margin: 0 auto;
}

.form-signin .form-control, .form-signup .form-input {
    position: relative;
    font-size: 16px;
    height: auto;
    padding: 10px;
    box-sizing: border-box;
    width: 100%;
}

.form-signin .form-control:focus, .form-signup .form-input:focus {
    z-index: 2;
}

.form-signin input[type="text"] {
    margin-bottom: -1px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}

.form-signin input[type="password"] {
    margin-bottom: 10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}

.new-account {
    font-weight: 900;
    color: #3a5999;
}
.login-here {
    font-weight: 900;
    color: #3a5999;
}
/* sign-up */
.form-signup .form-input {
    border: 1px solid #ccc;
    border-radius: 4px;
}

.text-help {
    color: #202B3D;
}
